import { Box, Card, CardHeader } from '@mui/material';
import React, { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
import { useSelector } from 'react-redux';

function ChartDht1() {
  const data = useSelector((state) => state.dhtChart);
  let arrayTemp = [];
  let arrayHumid = [];
  const arrayDate = [];
  data.forEach((item) => {
    arrayTemp.push(item.temp);
    arrayHumid.push(item.humid);
    arrayDate.push(new Date(Number(item.timestamp)).toISOString());
  });
  arrayTemp = Array.from(arrayTemp);
  arrayHumid = Array.from(arrayHumid);
  const [dataDhtChart] = useState({
    series: [
      {
        name: 'Nhit ',
        data: arrayTemp
      },
      {
        name: ' m',
        data: arrayHumid
      }
    ],
    options: {
      chart: {
        height: 450,
        type: 'area'
      },
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: 'smooth'
      },
      xaxis: {
        type: 'datetime',
        categories: arrayDate,
        labels: {
          datetimeUTC: false
        }
      },
      tooltip: {
        x: {
          format: 'dd/MM/yy HH:mm:ss'
        }
      },
      annotations: {
        yaxis: [
          {
            y: 32,
            borderColor: 'red',
            label: {
              show: true,
              text: 'Ngng nhit  bo ng',
              style: {
                color: '#fff',
                background: '#364cf4'
              }
            }
          },
          {
            y: 70,
            borderColor: 'yellow',
            label: {
              show: true,
              text: 'Ngng  m bo ng',
              style: {
                color: '#fff',
                background: '#364cf4'
              }
            }
          }
        ]
      }
    }
  });

  return (
    <Card>
      <CardHeader title="Biu  Nhit  V  m" subheader="Thi gian thc" />
      <Box sx={{ p: 2, pb: 1 }} dir="ltr">
        <ReactApexChart
          options={dataDhtChart.options}
          series={dataDhtChart.series}
          type="area"
          height={450}
        />
      </Box>
    </Card>
  );
}

export default ChartDht1;
