const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: "none",
  textAlign: "center",
  padding: theme.spacing(1, 0),
  color: theme.palette.info.darker,
  "&:hover": {
    boxShadow: "0 6px 20px 0 rgba(0, 0, 0, 0.19)",
  },
}));

function DashboardApp() {
  const [series, setSeries] = useState([[0], [0], [0], [0]]); //t,h,t1,h1
  const [checks, setChecks] = useState([
    false,
    false,
    false,
    false,
    false,
    false,
    false,
    false,
    false,
    false,
  ]);
  const { message } = useSubscription(["doan2/status"]);
  const json = { onOff: 1, iddv: 0, idtp: 2 };
  const divicesLed = [
    { pin: 1, deviceName: "Phng Khch", idtp: 2 },
    { pin: 2, deviceName: "Nh Tm", idtp: 2 },
    { pin: 3, deviceName: "Nh Bp", idtp: 2 },
    { pin: 4, deviceName: "Phng Ng", idtp: 2 },
    { pin: 5, deviceName: "M Tt C n", idtp: 4 },
    // { pin: 5, deviceName: "Ca Chnh", idtp: 3 },
  ];

  const divicesFan = [
    { pin: 6, deviceName: "Phng Khch", idtp: 5 },
    { pin: 7, deviceName: "Nh Tm", idtp: 5 },
    { pin: 8, deviceName: "Nh Bp", idtp: 5 },
    { pin: 9, deviceName: "Phng Ng", idtp: 5 },
    { pin: 10, deviceName: "M Tt C Qut", idtp: 6 },
    // { pin: 5, deviceName: "Ca Chnh", idtp: 3 },
  ];

  useEffect(() => {
    if (message) {
      const json = JSON.parse(message.message);

      if (
        json.t != null &&
        json.t1 != null &&
        json.h != null &&
        json.h1 != null
      )
        setSeries([[json.t], [json.h], [json.t1], [json.h1]]);
      setChecks([
        json.l[0] ? true : false,
        json.l[1] ? true : false,
        json.l[2] ? true : false,
        json.l[3] ? true : false,
        json.al ? true : false,
        json.f[0] ? true : false,
        json.f[1] ? true : false,
        json.f[2] ? true : false,
        json.f[3] ? true : false,
        json.af ? true : false,
      ]);
    }
  }, [message]);

  return (
    <Page title="Dashboard |  n 2">
      <Container maxWidth="xl">
        <Box sx={{ pb: 5 }}>
          <Typography style={{ textAlign: "center" }} variant="h4">
             n 2: Thnh Nhn, V Tng
          </Typography>
          <Typography style={{ textAlign: "center" }} variant="h4">
            H THNG IoT IU KHIN V GIM ST NGI NH
          </Typography>
        </Box>

        <Grid container spacing={3}>
          <Grid item xs={12} lg={12}>
            <Card style={{ boxShadow: "0 6px 20px 0 rgba(0, 0, 0, 0.19)" }}>
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Trong Nh
                </Typography>
                <Grid container item={true} xs={12} lg={12}>
                  <Grid item xs={12} sm={6}>
                    <Temp series={series[0]} />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Humid series={series[1]} />
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} lg={12}>
            <Card style={{ boxShadow: "0 6px 20px 0 rgba(0, 0, 0, 0.19)" }}>
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Ngoi Vn
                </Typography>
                <Grid container item={true} xs={12} lg={12}>
                  <Grid item xs={12} sm={6}>
                    <Temp series={series[2]} />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Humid series={series[3]} />
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} lg={6}>
            <ReactSwitch
              checks={checks}
              json={json}
              divices={divicesLed}
              groupName="n"
            />
          </Grid>
          <Grid item xs={12} lg={6}>
            <ReactSwitch
              checks={checks}
              json={json}
              divices={divicesFan}
              groupName="Qut"
            />
          </Grid>
          {/*
          <Grid item xs={12} sm={12} md={6}>
            <ChartRelay1 />
          </Grid>
          <Grid item xs={12} sm={12} md={6}>
            <ChartRelay2 />
          </Grid>
          <Grid item xs={12} md={6} lg={12}>
            <TableDHT />
          </Grid>
          <Grid item xs={12} md={6} lg={12}>
            <TableRelay1 />
          </Grid>
          <Grid item xs={12} md={6} lg={12}>
            <TableRelay2 />
          </Grid> */}
        </Grid>
      </Container>
    </Page>
  );
}

export default DashboardApp;
