import React from 'react';
import {
  DataGrid,
  gridPageCountSelector,
  gridPageSelector,
  useGridApiContext,
  useGridSelector,
  GridToolbar
} from '@mui/x-data-grid';
import Pagination from '@mui/material/Pagination';
import { Card, CardHeader, Box } from '@mui/material';
import { useSelector } from 'react-redux';

function CustomPagination() {
  const apiRef = useGridApiContext();
  const page = useGridSelector(apiRef, gridPageSelector);
  const pageCount = useGridSelector(apiRef, gridPageCountSelector);

  return (
    <Pagination
      color="primary"
      count={pageCount}
      page={page + 1}
      onChange={(event, value) => apiRef.current.setPage(value - 1)}
    />
  );
}

function TableDHT() {
  const data = useSelector((state) => state.dht);
  const columns = [
    { field: 'id', headerName: 'ID', width: 110, editable: true },
    {
      field: 'thietbi',
      headerName: 'Thit b',
      width: 100
    },
    { field: 'topic', headerName: 'Topic', width: 130, editable: false },
    {
      field: 'time',
      headerName: 'Thi gian',
      width: 160,
      type: 'date',
      editable: true
    },
    {
      field: 'temp',
      headerName: 'Nhit ',
      width: 90,
      type: 'number',
      editable: true
    },
    {
      field: 'humid',
      headerName: ' m',
      width: 100,
      type: 'number',
      editable: true
    },
    {
      field: 'warning',
      headerName: 'Bo ng',
      width: 150,
      editable: true,
      renderCell: (param) => (
        <h4
          style={{
            fontWeight: '700',
            color: param.row.warning ? '#990500' : '#009914'
          }}
        >
          {param.row.warning ? 'BO NG' : 'BNH THNG'}
        </h4>
      )
    }
  ];

  return (
    <Card>
      <CardHeader title="Bng Thng K Nhit  V  m" subheader="Nhit  V  m" />
      <Box sx={{ p: 2, pb: 1 }} dir="ltr">
        <div style={{ height: '550px', width: '100%' }}>
          <DataGrid
            rows={data}
            columns={columns}
            pageSize={8}
            rowsPerPageOptions={[10]}
            components={{
              Pagination: CustomPagination,
              Toolbar: GridToolbar
            }}
            pagination
          />
        </div>
      </Box>
    </Card>
  );
}

export default TableDHT;
